<template>
  <div class="main-ctn">
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <div class="main-content clearfix">
            <el-form :label-position="labelPosition" :inline="true" label-width="150px" :rules="rules" :model="editDetail" size="mini" ref="formName">
              <el-form-item label="" prop="subjectName">
                <el-input v-model="editDetail.subjectName" placeholder="" name="subjectName" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label=""  prop="staffname">
                <el-input v-model="editDetail.staffname" placeholder="" name="staffname" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label=""  prop="keyWord">
                <el-input v-model="editDetail.keyWord" placeholder="" name="keyWord"></el-input>
              </el-form-item>
              <el-form-item label="" prop="staffType">
              <template>
                <el-select v-model="editDetail.staffType" placeholder=""  >
                   <el-option v-for="item in staffType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
                </el-select>
              </template>
              </el-form-item>
              <el-form-item label="" prop="subjectGroupType">
              <template>
                <el-select v-model="editDetail.subjectGroupType" placeholder=""  >
                   <el-option v-for="item in subjectGroupType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
                </el-select>
              </template>
              </el-form-item>
              <el-form-item label="" prop="subjectClass">
              <template>
                <el-select v-model="editDetail.subjectClass" placeholder=""  >
                   <el-option v-for="item in subjectClass" :key="item.key" :label="item.value"  :value="item.key"></el-option>
                </el-select>
              </template>
              </el-form-item>
              <el-form-item label="" prop="weeklyType">
              <template>
                <el-select v-model="editDetail.weeklyType" placeholder="">
                   <el-option v-for="item in weeklyType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
                </el-select>
              </template>
              </el-form-item>
              <el-form-item label="" prop="weeklyDesc" style="height:calc(100% - 200px)">
                <div class="knowledge-content-editor" style="height:100%">
                  <textarea name="editor" id="edit"></textarea>
                </div>
              </el-form-item>
              <el-form-item label="" prop="nextWeeklyDesc" style="height:calc(100% - 200px)">
                <div class="knowledge-content-editor" style="height:100%">
                  <textarea name="editor" id="edito"></textarea>
                </div>
              </el-form-item>
              <el-form-item label="" prop="subjectNote" style="height:calc(100% - 200px)">
                <div class="knowledge-content-editor" style="height:100%">
                  <textarea name="editor" id="editor"></textarea>
                </div>
              </el-form-item>
              <el-upload class="upload-demo clearfix" ref="upload" action="www.baidu.com" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
               <div class="updateweekly"></div>
               <el-button slot="trigger" size="small" type="primary" class="selfile"></el-button>
               <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload(editDetail, filesList)"></el-button>
              </el-upload>
            </el-form>
            <span slot="footer" class="weekly-footer">     
              <el-button  @click="update(editDetail,'formName',filesList)"></el-button>
              <el-button type="primary" @click="deldetail()"></el-button>
            </span>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import service from "../scripts/service.js";
require("kindeditor");
require("kindeditor/themes/default/default.css");
export default {
  data: function() {
    return {
      rules: {
        keyWord: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        staffType: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        subjectGroupType: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        subjectClass: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        weeklyType: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]

        // weeklyDesc: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        // nextWeeklyDesc: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        // subjectNote: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ]
      },
      filesList: [
        {
          attachName: "",
          attachRemark: "www.baidu.com"
        },
        {
          attachName: "",
          attachRemark: "www.baidu.com"
        }
      ],
      weeklyType: [],
      subjectGroupType: [],
      subjectClass: [],
      staffType: [],
      weeklyType: [],
      staffname: "",
      subjectName: "",
      labelPosition: "right",
      weeklyList: {},
      Aauthor: "",
      staffID: "",
      editdetail: "",
      editDetail: {
        typeValue: ""
      }
    };
  },
  mounted() {
    this.getselect();
    this.getList();
    var options = {
      width: "99.5%",
      height: "200px",
      themeType: "simple",
      items: [
        "undo",
        "redo",
        "|",
        "selectall",
        "formatblock",
        "fontname",
        "fontsize",
        "|",
        "forecolor",
        "hilitecolor",
        "bold",
        "italic",
        "underline",
        "strikethrough",
        "lineheight",
        "removeformat",
        "|",
        "preview",
        "cut",
        "copy",
        "paste",
        "plainpaste",
        "|",
        "justifyleft",
        "justifycenter",
        "justifyright",
        "justifyfull",
        "insertorderedlist",
        "insertunorderedlist",
        "indent",
        "outdent",
        "subscript",
        "superscript",
        "|",
        "image",
        "hr",
        "code"
      ],

      resizeType: 1,
      newlineTag: "br",
      imageTabIndex: 1,
      uploadJson: "/KnowledgesController/savePic",
      filePostName: "file",
      allowImageUpload: true,
      allowFileManager: false,
      formatUploadUrl: true,
      allowImageRemote: false,
      afterUpload: function(url) {
        //console.log(url)
      }
    };
    var editor = new Array();
    KindEditor.ready(function(K) {
      editor[0] = K.create("#editor", options);
      editor[1] = K.create("#edit", options);
      editor[2] = K.create("#edito", options);
    });
  },
  methods: {
    getselect() {
      service
        .getListGroupType()
        .then(res => {
          this.subjectGroupType = res.data;
        })
        .catch(err => {
          console.log(err);
        });
      service
        .getListType()
        .then(res => {
          this.weeklyType = res.data;
        })
        .catch(err => {
          console.log(err);
        });
      service
        .getListStaffType()
        .then(res => {
          this.staffType = res.data;
          console.log(this.staffType);
        })
        .catch(err => {
          console.log(err);
        });
      service
        .getSubjectClass()
        .then(res => {
          this.subjectClass = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        weeklyId: this.$route.query.id
      };
      console.log(params);
      service
        .getWeeklyDetailInfo(params)
        .then(res => {
          console.log(res);
          // list
          this.editdetail = res.data;
          console.log(this.editdetail);
          (this.subjectName = this.$route.query.subjectName),
            (this.staffname = this.$route.query.staffname);
          this.staffID = this.$route.query.staffID;
          this.editDetail = {
            subjectName: this.subjectName,
            staffname: this.staffname,
            createStaffId: this.staffID,
            keyWord: this.editdetail.keyWord,
            staffType: this.editdetail.staffType,
            subjectClass: this.editdetail.subjectClass,
            subjectGroupType: this.editdetail.subjectGroupType,
            weeklyType: this.editdetail.weeklyType
          };
          KindEditor.instances[0].html(this.editdetail.subjectNote),
            KindEditor.instances[1].html(this.editdetail.weeklyDesc),
            KindEditor.instances[2].html(this.editdetail.nextWeeklyDesc);
          console.log(this.editDetail);
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitUpload(editDetail, filesList) {
      this.$refs.upload.submit();
      let params = {
        weeklyId: this.$route.query.id,
        cwlas: this.filesList
      };
      console.log(params);
      service
        .addWeekLyAttachyInfo(params)
        .then(response => {})
        .catch(error => {
          console.log(error);
        });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    // 
    deldetail() {
      window.close();
    },
    //
    update(editDetail, formName) {
      let subjectNote = KindEditor.instances[0]
        .html()
        .split("\n")
        .join("");
      console.log(subjectNote);
      let weeklyDesc = KindEditor.instances[1]
        .html()
        .split("\n")
        .join("");
      console.log(weeklyDesc);
      let nextWeeklyDesc = KindEditor.instances[2]
        .html()
        .split("\n")
        .join("");
      console.log(this.editDetail.subjectClass);
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.editDetail = {
            weeklyId: this.$route.query.id,
            subjectName: this.subjectName,
            createStaffId: this.staffID,
            subjectNote: subjectNote,
            weeklyDesc: weeklyDesc,
            nextWeeklyDesc: nextWeeklyDesc,
            keyWord: this.editDetail.keyWord,
            staffType: this.editDetail.staffType,
            subjectClass: this.editDetail.subjectClass,
            subjectGroupType: this.editDetail.subjectGroupType,
            weeklyType: this.editDetail.weeklyType
          };

          let params = {
            ckl: this.editDetail
          };
          console.log(params);
          service
            .updateWeeklyInfo(params)
            .then(response => {
              alert("");
              window.close();
              // window.close();
            })
            .catch(error => {
              console.log(error);
            });
          alert("submit!");
        } else {
          return false;
          this.$message("");
        }
      });
    }
  }
};
</script>

<style scoped>
.main-ctn {
  background: #f8f8f8;
}
.bg-purple {
  background: #ffffff;
  border-radius: 8px;
}
.main-content {
  padding-top: 30px;
}
.el-form-item {
  margin-top: 5px;
}
.main-content .dialog-footer {
  margin: 0 auto;
}
.weekly-footer {
  float: left;
  margin-left: 40% !important;
  margin-bottom: 20px !important;
}
.knowledge-content-editor {
  width: 110%;
}
.updateweekly {
  margin-top: 5px;
  float: left;
  font-weight: 400;
  color: #66666f;
}
.selfile {
  margin-left: 10px !important;
}
.upload-demo {
  margin-left: 58px;
  width: 80%;
  padding-bottom: 20px;
}
</style>


